@import "./variables.scss";

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, Tahoma, sans-serif;
  font-style: normal;
  font-weight: 500;

  color: $text-color;
}

ul,
ol {
  list-style: none;
}
a {
  text-decoration: none;
  color: $text-color;
}
